export function set_locale(new_locale: string) {
    if (new_locale.toLowerCase().includes('ru')) {
        strings.setLanguage('ru')
        return 'ru'
    } else {
        strings.setLanguage('en')
        return 'en'
    }
}

export function init_locale() {
    let locale = queryString.parse(document.location.search)['lang'];
    if (!locale) {
        locale = getUserLocale()
    }

    return set_locale(locale)
}

function get_experience() {
    let now = new Date();
    let start_work = new Date("2018-11");

    let years = now.getFullYear() - start_work.getFullYear();
    let months = now.getMonth() - start_work.getMonth();
    if (months < 0) {
        months = 12 + months;
        years -= 1;
    }
    let res = '';

    if (years > 0) {
        res += years.toString() + ' yr'
    }

    if (months > 0) {
        if (years > 0) {
            res += ' '
        }
        res += months.toString() + ' mos'
    }
    return res;
}

